@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --animation-left: -100%;
  --animation-right: 100%;
  --animation-top: -100%;
  --animation-bottom: 100%;
  --columns: 12;
  --adaptive-box: 100%;
  --body-font-size: 0.14rem;
  --gap-x: 0.1rem;
  --gap-y: 0.15rem;
  --container-gap: 0.1rem;
}
@media (min-width: 1024px) {
  :root {
    --body-font-size: 0.18rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --gap-x: 0.3rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --gap-y: 0.3rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --container-gap: 0;
  }
}
body {
  line-height: 1.35;
  font-weight: 400;
  font-size: var(--body-font-size);
  font-family: "Poppins", sans-serif;
  margin: 0;
  color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  text-size-adjust: none;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  transition-duration: 400ms;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

iframe {
  border: 0;
}

b, strong {
  font-weight: 700;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  padding: 0;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  background: transparent;
  border: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: number-input;
}

button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

input::placeholder,
textarea::placeholder {
  opacity: 1;
  color: currentColor;
}

:root {
  --width-max-window: 100vw;
  --width-min-window: 320;
  --width-min-window-px: 320px;
  --min-value: 100;
  --min-value-px: 100px;
  --max-value: 150;
  --layout-size: 480;
  font-size: calc(var(--min-value-px) + (var(--max-value) - var(--min-value)) * (var(--width-max-window) - var(--width-min-window-px)) / (var(--layout-size) - var(--width-min-window)));
}
@media (min-width: 480px) {
  :root {
    --width-max-window: 440px;
  }
}
@media (min-width: 640px) {
  :root {
    --width-max-window: 520px;
  }
}
@media (min-width: 768px) {
  :root {
    --width-max-window: 768px;
    --layout-size: 1920;
  }
}
@media (min-width: 1024px) {
  :root {
    --width-max-window: 100vw;
    --width-min-window: 960;
    --width-min-window-px: 960px;
    --min-value: 50;
    --min-value-px: 50px;
    --max-value: 100;
  }
}
@media (min-width: 1920px) {
  :root {
    --width-max-window: 1920px;
  }
}

.grid-flex,
.grid {
  --grid-gap-x: var(--gap-x);
  --grid-gap-y: var(--gap-y);
  --grid-columns: var(--columns);
}

.container-full, .container\@lg, .container\@md, .container\@xs, .container {
  --local-container-gap: var(--container-gap);
  --dp-block: block;
  display: var(--dp-block);
  width: 100%;
  padding-left: var(--local-container-gap);
  padding-right: var(--local-container-gap);
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 480px) {
  .container {
    max-width: 440px;
  }
}
@media (min-width: 640px) {
  .container\@xs, .container {
    max-width: 520px;
  }
}
@media (min-width: 768px) {
  .container\@md, .container, .container\@xs {
    max-width: 720px;
  }
}
@media (min-width: 1024px) {
  .container\@lg, .container, .container\@md, .container\@xs {
    max-width: 12rem;
  }
}

br {
  --dp-inline: inline;
  display: var(--dp-inline);
}

.icon {
  width: 1em;
  height: 1em;
  --dp-block: block;
  display: var(--dp-block);
  flex: 0 0 auto;
  fill: currentColor;
  transition: all 400ms 0s;
  position: relative;
}
.icon--no-fill {
  fill: none;
}
.icon--stroke {
  stroke: currentColor;
}
.icon--only-stroke {
  fill: none;
  stroke: currentColor;
}

.loader {
  height: 1em;
  width: 1em;
  display: block;
  overflow: hidden;
  position: relative;
}
.loader:after, .loader:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 3px solid currentColor;
  border-radius: 50%;
}
.loader:after {
  opacity: 0.15;
}
.loader:before {
  border-bottom-color: transparent;
  border-top-color: transparent;
  animation: loading 1.5s linear infinite;
}
.loader--center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
.loader--size-sm {
  font-size: 0.4rem;
}
.loader--size-md {
  font-size: 0.6rem;
}
.loader--size-lg {
  font-size: 0.8rem;
}

.adaptive-box {
  position: relative;
  width: 100%;
  --dp-block: block;
  display: var(--dp-block);
}
.adaptive-box:after {
  content: "";
  padding: 0px 0px var(--adaptive-box) 0px;
  width: 100%;
  display: block;
}
.adaptive-box > * {
  position: absolute;
}
.adaptive-box__image, .adaptive-box__item {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.adaptive-box__image {
  object-fit: cover;
}

.link {
  color: currentColor;
}
@media (pointer: fine) {
  .link:hover {
    color: #0091bd;
  }
  .link:active {
    color: red;
  }
}
.link:active {
  color: red;
}
.link-decor {
  color: currentColor;
  position: relative;
}
.link-decor:before {
  content: "";
  position: absolute;
  bottom: -2px;
  width: 100%;
  border-bottom: 1px solid currentColor;
  transform: scaleX(0);
  transition-duration: 400ms;
}
@media (pointer: fine) {
  .link-decor:hover:before {
    transform: scaleX(1);
  }
}
.link--line {
  text-decoration: underline;
}

.list li {
  position: relative;
  padding-left: 0.15rem;
}
.list li:not(:last-child) {
  margin-bottom: 0.1rem;
}
.list li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.08rem;
  height: 1em;
  width: 1em;
  font-size: 0.08rem;
  border-radius: 50%;
  background-color: #000;
}

.animation {
  animation-play-state: paused;
  animation-duration: 2s;
  animation-fill-mode: both;
}
.animation.start {
  animation-play-state: running;
}

@keyframes loading {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loading-n {
  100% {
    transform: rotate(-720deg);
  }
}
.title {
  --dp-block: block;
  display: var(--dp-block);
  font-weight: 700;
  line-height: 1.15;
  word-wrap: break-word;
}
.title--size-sm {
  font-size: 0.24rem;
}
@media (min-width: 1024px) {
  .title--size-sm {
    font-size: 0.36rem;
  }
}
.title--size-md {
  font-size: 0.24rem;
}
@media (min-width: 1024px) {
  .title--size-md {
    font-size: 0.56rem;
  }
}
.title--size-lg {
  font-size: 0.24rem;
}
@media (min-width: 1024px) {
  .title--size-lg {
    font-size: 0.72rem;
  }
}

.btn {
  position: relative;
  text-decoration: none;
  transition-duration: 400ms;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.btn-primary {
  --dp-flex: flex;
  display: var(--dp-flex);
  align-items: center;
  justify-content: center;
  font-weight: 500;
  border-radius: 500px;
}
.btn-primary._icon {
  justify-content: space-between;
}
.btn-primary--sm {
  min-height: 0.5rem;
  padding: 0.1rem 0.2rem;
}
.btn-primary--md {
  min-height: 0.6rem;
  padding: 0.1rem 0.25rem;
}
.btn-primary--lg {
  min-height: 0.7rem;
  padding: 0.1rem 0.3rem;
}

.btn-circle {
  --dp-flex: flex;
  display: var(--dp-flex);
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  height: 1em;
  width: 1em;
  font-size: 2rem;
  border-radius: 50%;
}
.btn-circle > span {
  font-size: initial;
}

.btn-play {
  --dp-flex: flex;
  display: var(--dp-flex);
  align-items: center;
  justify-content: center;
  height: 1em;
  width: 1em;
  font-size: 1rem;
  border-radius: 50%;
}
.btn-play .icon {
  font-size: 0.36em;
}

.btn--theme-primary {
  background-color: #000;
  color: #fff;
}
.btn--theme-primary .icon {
  color: #fff;
}
@media (pointer: fine) {
  .btn--theme-primary:hover {
    background-color: #444444;
  }
  .btn--theme-primary:active {
    background-color: #848484;
  }
}
.btn--theme-primary:active {
  background-color: #848484;
}

.btn--theme-outline {
  border: 1px solid #000;
  color: #000;
}
.btn--theme-outline .icon {
  color: #000;
}
@media (pointer: fine) {
  .btn--theme-outline:hover {
    color: #fff;
    background: #000;
  }
  .btn--theme-outline:hover .icon {
    color: #fff;
  }
  .btn--theme-outline:active {
    color: #fff;
    border-color: #848484;
    background: #848484;
  }
  .btn--theme-outline:active .icon {
    color: #fff;
  }
}
.btn--theme-outline:active {
  color: #fff;
  border-color: #848484;
  background: #848484;
}
.btn--theme-outline:active .icon {
  color: #fff;
}

.form {
  display: flex;
  flex-direction: column;
}

.form-field {
  --dp-flex: flex;
  display: var(--dp-flex);
  align-items: center;
  position: relative;
  width: 100%;
  font-size: 0.18rem;
  border: 1px solid #000;
  --field-input-color: #000;
}
.form-field__input {
  color: var(--field-input-color);
  width: 100%;
  display: block;
}
.form-field__input::placeholder {
  opacity: 0;
}
.form-field__input:focus ~ .form-field__text, .form-field__input:not(:placeholder-shown) ~ .form-field__text {
  opacity: 0;
}
.form-field__input:-webkit-autofill {
  -webkit-text-fill-color: var(--field-input-color);
  transition: background-color 500000s ease-in-out 0s;
}
.form-field__error, .form-field__input, .form-field__required, .form-field__text {
  font-size: 1em;
  transition-duration: 400ms;
  padding: 0.25rem 0.4rem;
}
.form-field__error, .form-field__required, .form-field__text {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  width: 100%;
}
.form-field__text {
  color: currentColor;
}
.form-field__text span {
  color: #000;
}
.form-field__error, .form-field__required {
  color: #000;
  width: 100%;
  opacity: 0;
}
.form-field._required > * {
  opacity: 0;
}
.form-field._required .form-field__required {
  opacity: 1;
}
.form-field._error > * {
  opacity: 0;
}
.form-field._error .form-field__error {
  opacity: 1;
}

.form-file {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  width: 100%;
}
.form-file__controller {
  display: flex;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
  color: #fff;
  width: 100%;
  font-size: 0.24rem;
  height: 0.8rem;
  padding: 0 0.4rem;
  transition-duration: 400ms;
}
.form-file__controller:hover {
  transition-duration: 400ms;
}
.form-file__input {
  display: none;
}
.form-file__quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 0px;
  overflow: hidden;
  transition-duration: 100ms;
}
.form-file__quantity._active {
  padding-left: 15px;
  max-width: none;
}
.form-file__quantity._active .form-file__loader {
  opacity: 0;
  transition-delay: 375ms;
}
.form-file__quantity._active .form-file__number,
.form-file__quantity._active .form-file__icon {
  opacity: 1;
  transition-delay: 375ms;
}
.form-file__loader {
  position: absolute;
  z-index: 2;
  transition-duration: 150ms;
}
.form-file__number {
  margin-left: 6px;
  opacity: 0;
  transition-duration: 300ms;
}
.form-file__icon {
  opacity: 0;
  transition-duration: 300ms;
}

.form-textarea {
  position: relative;
  display: block;
  border: 1px solid rgb(185, 185, 185);
  transition-duration: 400ms;
}
.form-textarea__input {
  resize: none;
}
.form-textarea__input::placeholder {
  opacity: 0;
}
.form-textarea__input:focus ~ .form-textarea__text, .form-textarea__input:not(:placeholder-shown) ~ .form-textarea__text {
  opacity: 0;
}
.form-textarea__input, .form-textarea__required, .form-textarea__text {
  width: 100%;
  font-size: 1em;
  transition-duration: 400ms;
  padding: 0.25rem 0.4rem;
}
.form-textarea__required, .form-textarea__text {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  pointer-events: none;
}
.form-textarea__text span {
  color: #000;
}
.form-textarea__required {
  opacity: 0;
}
.form-textarea._required {
  border-color: #000;
}
.form-textarea._required .form-textarea__text {
  opacity: 0;
}
.form-textarea._required .form-textarea__required {
  opacity: 1;
}

.form-radio-group {
  display: flex;
  flex-wrap: wrap;
}

.form-radio {
  margin: 0 size-x(20) size-x(20) 0;
  display: block;
  position: relative;
}
.form-radio__input {
  opacity: 0;
  font-size: 0;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  cursor: pointer;
}
.form-radio__input:hover ~ .form-radio__controller {
  background-color: #000;
  color: #fff;
}
.form-radio__input:checked {
  cursor: auto;
}
.form-radio__input:checked ~ .form-radio__controller {
  background-color: #000;
  color: #fff;
}
.form-radio__box {
  position: relative;
  z-index: 1;
  border: 1px solid currentColor;
  transition-duration: 400ms;
  padding: size-x(18) size-x(42);
  display: block;
}

/*
<span class="form-checkbox">
    <input class="form-checkbox__input" type="checkbox">
    <span class="form-checkbox__decor"></span>
    <svg class="icon"><use xlink:href="svg-sprite/sprite.svg#close"></use></svg>
</span>
 */
.form-checkbox {
  position: relative;
  display: inline-flex;
  flex: 0 0 auto;
  font-size: 0.28rem;
  align-items: center;
  justify-content: center;
}
.form-checkbox__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  cursor: pointer;
}
.form-checkbox__input:checked ~ .form-checkbox__decor {
  border-color: #000;
  --checkbox-decor-size:1;
}
.form-checkbox__input:checked ~ .icon {
  opacity: 1;
}
.form-checkbox__decor {
  position: relative;
  height: 1em;
  width: 1em;
  border: 1px solid #000;
  --checkbox-decor-size: 0;
}
.form-checkbox__decor:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transition-duration: 400ms;
  height: 0.714em;
  width: 0.714em;
  transform: translate3d(-50%, -50%, 0) scale(var(--checkbox-decor-size));
  background-color: #000;
}
.form-checkbox--icon {
  border: 1px solid #000;
  color: #000;
  height: 1em;
  width: 1em;
}
.form-checkbox--icon .icon {
  position: absolute;
  opacity: 0;
  font-size: 0.6em;
  margin-top: 0.05em;
  transition-timing-function: linear;
}

.form-send {
  position: relative;
  z-index: 20;
}
.form-send__message {
  display: none;
  position: absolute;
  padding: 1em;
  width: 100%;
  left: 0;
  bottom: calc(100% + 8px);
  border-radius: 0.12rem;
  text-align: center;
  box-shadow: 0px 4px 0.4rem rgba(28, 46, 64, 0.06);
  background: #000;
  color: #fff;
}
.form-send__message:before {
  content: "";
  position: absolute;
  border: 8px solid transparent;
  border-top: 8px solid #000;
  top: 100%;
  left: calc(50% - 8px);
}
.form-send .btn {
  width: 100%;
}
.form-send__loading {
  position: relative;
}
.form-send__loading span {
  height: 2em;
  width: 2em;
  border-radius: 50%;
  border: 2px solid currentColor;
  display: block;
  margin: 0 auto;
  border-bottom: 0;
  border-right: 0;
  animation: loading 0.5s infinite linear;
}
.form-send__success {
  display: none;
}
.form-send__error {
  display: none;
}

