  height: 100vh;
  z-index: 899;
  background: #fff;
  overflow: hidden;
  display: none;
}
.menu-modal__wrapper {
  display: flex;
  height: 100%;
  width: 100%;
  padding: 0.3rem 0;
  overflow-x: hidden;
  overflow-y: auto;
}
.menu-modal__box {
  display: flex;
  flex: 1;
}
